@import '../../assets/scss/common.scss';

$class-prefix: "add-task";

.#{$class-prefix} {
  &_top {
    padding: 0 40px;
  }
  &_tasks,
  &_header {
    @include header;
  }
  &_tasks {
    padding-top: 15px;
    margin-bottom: 15px;
  }
  &_title {
    margin: 0;
    font-weight: 200;
    color: #fff;
  }
  &_input-icon {
    cursor: pointer;
  }
}